.Toast {
    position: relative;
    width: 100%;
    height: 0;
    line-height: 38px;
    top: 0;
    opacity: 0;
    font-size: 0.9rem;
    font-weight: 300;
    text-align: center;
    color: white;
    background-color: rgb(90, 0, 0);
    cursor: pointer;
}
.Toast:hover{
    height: 100px;
}